/*global kakao*/
import React from "react";
import $ from "jquery";

function getMap() {
  let container = document.getElementById("myMap");
  let options = {
    center: new kakao.maps.LatLng(36.503603, 127.250751),
    level: 12,
  };
  let map = new window.kakao.maps.Map(container, options);

  function addControl(map) {
    const zoomControl = new kakao.maps.ZoomControl();
    map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
  }

  async function getData(map) {
    await fetch(
      "https://atomy-kr-education-center-default-rtdb.asia-southeast1.firebasedatabase.app/.json"
    )
      .then((response) => response.json())
      .then((data) => {
        const clusterer = new kakao.maps.MarkerClusterer({
          map: map,
          averageCenter: true,
          minLevel: 8,
        });

        const markers = $(data).map(function (i, data) {
          const coords = new kakao.maps.LatLng(data.lat, data.lng);

          const marker = new kakao.maps.Marker({
            map: map,
            position: coords,
          });

          //커스텀오버레이
          const content = `<div class="overlay_info">
                            <p class="title">센터명 : <strong>${data.centerName}</strong></p>
                            <div class="desc">
                              <span class="address">${data.address1}&nbsp;${data.address2}</span>
                            </div>
                            <button class="btn-close" type="button">X</button>
                          </div>`;

          // 커스텀 오버레이를 생성합니다
          const mapCustomOverlay = new kakao.maps.CustomOverlay({
            position: marker.getPosition(),
            content: content,
            xAnchor: 0.5,
            yAnchor: 1.3,
          });

          kakao.maps.event.addListener(marker, "click", function () {
            mapCustomOverlay.setMap(map);
            map.setCenter(coords);
            map.setLevel(6);

            $(".btn-close").on("click", function () {
              mapCustomOverlay.setMap(null);
            });
          });

          return marker;
        });

        clusterer.addMarkers(markers);

        //--------------------------------------------
      })
      .catch((error) => console.log(error));
  }

  function currentLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(function (position) {
        const lat = position.coords.latitude;
        const lon = position.coords.longitude;
        const locPosition = new kakao.maps.LatLng(lat, lon);
        displayMarker(locPosition);
      });
    } else {
      const locPosition = new kakao.maps.LatLng(33.450701, 126.570667);
      displayMarker(locPosition);
    }

    function displayMarker(locPosition) {
      var imageSrc = "https://ssl.pstatic.net/static/maps/m/pin_rd.png";
      var imageSize = new kakao.maps.Size(14, 14);
      var imageOption = { offset: new kakao.maps.Point(14, 14) };

      var markerImage = new kakao.maps.MarkerImage(
        imageSrc,
        imageSize,
        imageOption
      );

      const marker = new kakao.maps.Marker({
        map: map,
        image: markerImage,
        position: locPosition,
      });
      map.setLevel(4);
      map.setCenter(locPosition);
      marker.setMap(map);
    }
  }

  addControl(map);
  getData(map);
  // const btnCurrentLocation = document.querySelector(".btn-current-location");
  // btnCurrentLocation.addEventListener("click", currentLocation);

  $(".btn-current-location").one("click", currentLocation);
}

kakao.maps.load(() => getMap());

function SetMap() {
  return <div id="myMap" />;
}

export default SetMap;
