import React from "react";

function Card({ center }) {
  return (
    <li>
      <a
        href="#"
        onClick={() => {
          console.log(center.lat, center.lat);
        }}
      >
        <strong>{center.centerName}</strong>
        <br></br>
        {center.address1}
        {center.address2}
      </a>
    </li>
  );
}

export default Card;
